@font-face {
  font-family: 'Icomoon';
  src: url('./../Vendors/icomoon/fonts/icomoon.eot');
  src: url('./../Vendors/icomoon/fonts/icomoon.eot') format('embedded-opentype'), url('./../Vendors/icomoon/fonts/icomoon.ttf') format('truetype'),
    url('./../Vendors/icomoon/fonts/icomoon.woff') format('woff'), url('./../Vendors/icomoon/fonts/icomoon.svg') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^='icon-'],
[class*=' icon-'] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'Icomoon' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

